<template>
    <div class="myRanking">
         <!-- 顶部导航栏 -->
        <van-sticky>
              
                   <van-nav-bar>
                        <template #left>
                                <span class="iconfont icon-arrow-fine-up-copy" @click.stop="onClickLeft"></span>
                        </template>
                    </van-nav-bar>
                    <div class="iconfont icon-paihangbang1 paihangbang" ></div>

             
        </van-sticky>
       
        <div class="panBox" ref="panBox">
            <div class="panItem" v-for="(item,index) in nowList" :key="index" @click="goPater(item.id,item.score)">

                <van-skeleton title row-width=100% :row="1" :loading="loading">
                    <span class="sHeard" :style="{'color' : (index <=2 ? '#ff4338' : '#000') }">{{index+1}}</span>
                </van-skeleton>
                
                <van-skeleton title avatar :loading="loading">
                    <img v-lazy="item.img1v1Url" class="imgs">
                </van-skeleton>
                
                <div class="sBodeys">
                    <van-skeleton title row-width=100% :row="1" :loading="loading">
                        <p class="score">{{item.score | ff_score}}</p>
                        <p>
                            <span class="songs">歌手</span>
                            <span class="geshou">{{item.name}}</span>
                            <span class="iconfont icon-shangsheng" v-if="item.topicPerson > 80000"></span>
                        </p>
                    </van-skeleton> 
                </div>
                <van-skeleton title row-width=100% :row="1" :loading="loading">
                     <span class="iconfont icon-sandian"></span>
                </van-skeleton>
            </div>

            <van-loading size="24px" v-if="isDi == 1">加载中...</van-loading>
            <van-divider v-if="isDi == 2" dashed>我也是有底线的</van-divider>
        </div>
    </div>
</template>

<script>
    import '../assets/less/Ranking.less'
    export default {
        name : 'Ranking',
        data() {
            return {
                artistsList : [],
                limit       : 20,
                nowList     : [],
                isDi        : 0,
                time        : null,
                isNum       : 0,
                loading     : true,
            }
        },
        methods:{  
            onClickLeft(){
                window.removeEventListener('scroll',this.ff_scroll)
                this.$router.go(-1);
            },
            goPater(id,score){
                window.removeEventListener('scroll',this.ff_scroll)
                this.$router.push({name:'Songlist',query:{id,score}});
                    console.log(id)
            },
            // 请求歌手
            ff_requet(){
                this.axios({
                    url : '/toplist/artist',
                    params : {
                    type   : 1
                    }
                })
                .then(res=>{
                    if(res.data.code == 200){
                        this.$toast.clear();
                        this.artistsList =  res.data.list.artists,
                        this.nowList = this.artistsList.slice(0,20);
                        setTimeout(()=>{
                               this.loading = false;
                        },500)
                        
                    }
                    
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            // 滚动的事件
            ff_scroll(e){
                let cishu     = this.artistsList.length / this.limit;
                let winHeight = document.body.offsetHeight;
                if(this.$refs.panBox){
                    let maxHeight = this.$refs.panBox.offsetHeight + this.$refs.panBox.offsetTop;
                    let chaHeight = maxHeight - winHeight;
                    if(e.path[1].pageYOffset >= chaHeight){
                            this.isDi = 1;
                            clearTimeout(this.time)
                            this.time = setTimeout(()=>{
                                this.isNum++;
                                this.isDi = 0;

                                this.limit = this.limit + 20;
                                this.nowList = this.artistsList.slice(0,this.limit);
                                console.log(this.isNum>=cishu)
                             
                            },500);
                }

                }
                

                
               
            }
        },
        created() {
             this.$toast.loading({
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
            });
            // 请求歌手
            this.ff_requet();

            
        },
        filters:{
            ff_score(val){
                let str = `${Math.floor(val/10000)}万`
                return str;
            }
        },
         watch: {
            nowList(news,olds){
                if(news.length){
                    window.addEventListener('scroll',this.ff_scroll)
                }
            }
        },
        
    }
</script>

<style lang="less" scoped>

</style>